html {
	scroll-behavior: smooth;
	overflow-x: hidden;
}

body::-webkit-scrollbar {
	display: None;
}

@media (max-width: 767px) {
	html {
		font-size: 80%;
	}
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: 'Raleway', sans-serif;
}

#header {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	z-index: 1;
	padding: 15px;
	overflow-y: auto;
}

.nav-menu * {
	margin: 0;
	padding: 0;
	list-style: none;
}

.nav-menu > ul > li {
	position: relative;
	white-space: nowrap;
}

.nav-menu a:hover,
.nav-menu .active,
.nav-menu .active:focus,
.nav-menu li:hover > a {
	color: #fff;
	background: #0563bb;
	border-radius: 50px;
}

.nav-menu a,
.nav-menu a:focus {
	display: flex;
	align-items: center;
	color: #45505b;
	padding: 10px 18px;
	margin-bottom: 8px;
	transition: 0.3s;
	font-size: 15px;
	border-radius: 50px;
	background: #f2f3f5;
	height: 56px;
	width: 100%;
	overflow: hidden;
	flex-direction: column;
}

.nav-menu a i,
.nav-menu a:focus i {
	font-size: 20px;
}

.nav-item a:hover,
.nav-item li:hover > a {
	width: 100%;
	color: #fff;
}

.nav-item a:hover span,
.nav-item li:hover > a span {
	display: block;
}

.nav-item a span,
.nav-item a:focus span {
	padding: 0 5px 0 7px;
	color: #45505b;
}

.nav-item a:hover span,
.nav-item .active span,
.nav-item .active:focus span,
.nav-item li:hover > a span {
	color: #fff;
}

.nav-item a:hover,
.nav-item li:hover > a {
	width: 100%;
	color: #fff;
}

.nav-item a span,
.nav-item a:focus span {
	display: none;
	color: #fff;
}
.nav-menu a,
.nav-menu a:focus {
	width: 56px;
}

#hero {
	padding-left: 130px;
	width: 100%;
	height: 100vh;
	background: url(../assets/images/HeroScreen2.png) right no-repeat;
	background-size: cover;
	position: relative;
}

#hero h1 {
	margin: 0;
	font-size: 64px;
	font-weight: 700;
	line-height: 56px;
	color: #45505b;
}

#hero p {
	color: #45505b;
	margin: 15px 0 0 0;
	font-size: 26px;
	font-family: 'Poppins', sans-serif;
}

#hero:before {
	content: '';
	background: rgba(255, 255, 255, 0.7);
	position: absolute;
	bottom: 0;
	top: 0;
	left: 0;
	right: 0;
}

#hero p span {
	color: #0563bb;
}

@media (max-width: 767px) {
	#hero {
		padding-left: 30px;
		background: url(../assets/images/test4.png) top left no-repeat;
	}
	#hero h1 {
		font-size: 44px;
		line-height: 36px;
	}
	#hero p {
		margin-top: 10px;
		font-size: 22px;
		line-height: 24px;
	}
}

section {
	padding: 50px 6rem;
}

.title-text {
	font-size: 32px;
	font-weight: bold;
	color: #45505b;
	position: relative;
	margin-bottom: 15px;
	padding-bottom: 10px;
}

.section-header h2::after {
	content: '';
	position: absolute;
	display: block;
	width: 140px;
	height: 3px;
	background: #0563bb;
	bottom: 0;
	left: calc(50% - 70px);
}

.section-header {
	margin-bottom: 30px;
}

.section-header p {
	font-family: 'Poppins', sans-serif;
}

.section-content h3 {
	font-weight: 700;
	font-size: 26px;
	color: #728394;
	margin-bottom: 1rem;
}

.section-content ul {
	list-style: none;
	padding: 0;
}

.section-content ul li {
	margin-bottom: 20px;
	display: flex;
	align-items: center;
}

.section-content ul i {
	font-size: 18px;
	margin-right: 5px;
	color: #0563bb;
	line-height: 0;
}

.section-content ul strong {
	margin-right: 5px;
}

.skills .progress {
	height: 60px;
	display: block;
	background: none;
	border-radius: 0;
}
.skills .progress .skill {
	padding: 10px 0;
	margin: 0;
	display: block;
	font-weight: 600;
	font-family: 'Poppins', sans-serif;
	color: #45505b;
}

.skills .progress .skill .val {
	float: right;
	font-style: normal;
}

.progress-div {
	background-color: #f2f3f5;
	border-radius: 10px;
}

.progress-loaded {
	background-color: #0563bb;
	height: 10px;
	transition: 0.9s ease-in-out;
	transition-delay: 2s;
	border-radius: 10px;
}

.portfolio-title {
	font-size: 26px;
	font-weight: bold;
	color: #45505b;
}

.portfolio-link:hover .portfolio-title {
	color: #0563bb;
}

.portfolio-body {
	font-size: 14px;
}

#resumeContainer {
	margin: auto;
	display: flex;
	justify-content: center;
}

.PDFDocument {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.PDFPage {
	display: flex;
	justify-content: center;
	box-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
}

.annotationLayer {
	position: absolute;
	height: 100%;
}

.PDFDownload {
	position: absolute;
	bottom: 0;
	right: 10px;
}

.PDFDownload a {
	display: flex;
	align-items: center;
	flex-direction: column;
	text-decoration: none;
	color: gray;
}

.PDFDownload a:hover {
	color: #0563bb;
}

.PDFDownload i {
	font-size: 35px;
}

.PDFDownload strong {
	font-size: 13px;
}

.PDFDownloadMD a {
	display: flex;
	align-items: center;
	flex-direction: column;
	text-decoration: none;
	color: gray;
}

.PDFDownloadMD i {
	font-size: 80px;
}

.PDFDownloadMD strong {
	font-size: 20px;
}

@media (max-width: 991px) {
	.PDFPage .react-pdf__Page__canvas,
	.PDFPage .react-pdf__Page__textContent {
		width: 612px !important;
		height: 792px !important;
	}

	.PDFPage .react-pdf__Page__textContent {
		display: none;
	}

	.PDFDownload i {
		font-size: 29.5px;
	}

	.PDFDownload strong {
		font-size: 10px;
	}
}

.MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
	border-color: #0563bb !important;
}

.MuiInputLabel-outlined.MuiInputLabel-shrink {
	transform: translate(12px, -2px) scale(0.75) !important;
}

.MuiFormLabel-root.Mui-focused {
	color: #0563bb !important;
}

.social-links a {
	font-size: 20px;
	background: #45505b;
	color: #fff;
	line-height: 1;
	padding: 8px 0;
	margin-right: 10px;
	border-radius: 50%;
	text-align: center;
	width: 36px;
	height: 36px;
}

.social-links a:hover {
	color: #fff;
	background: #0563bb;
}

.sendBtn {
	color: #0563bb !important;
	border-color: #0563bb !important;
}

.sendBtn:hover {
	background-color: #0563bb !important;
	color: white !important;
}

#footer {
	background: #f7f8f9;
	color: #45505b;
	font-size: 14px;
	text-align: center;
	padding: 50px 5rem;
}

#footer h3 {
	font-size: 36px;
	font-weight: 700;
	position: relative;
	font-family: 'Poppins', sans-serif;
	padding: 0;
	margin: 0;
}

#footer p {
	font-style: italic;
	padding: 0;
	margin: 0 0 40px 0;
}

/**
* Mobile
*/
.mobile-nav-toggle {
	position: fixed;
	right: 2px;
	top: 10px;
	z-index: 9998;
	border: 0;
	background: none;
	font-size: 28px;
	transition: all 0.4s;
	outline: none !important;
	line-height: 0;
	cursor: pointer;
	border-radius: 50px;
	padding: 5px;
	font-size: 35px;
}

.mobile-nav-toggle:hover {
	color: #0563bb;
}

.mobile-nav-active {
	overflow: hidden;
}

.mobile-nav-active #header {
	left: 0;
}

.mobile-nav-active #header .navbar {
	display: block;
}

.mobile-nav-active #header .nav-menu a,
.mobile-nav-active #header .nav-menu a:focus {
	flex-direction: row;
	width: 100%;
}

.mobile-nav-active #header .nav-item a span,
.mobile-nav-active #header .nav-item a:focus span {
	display: block;
}

.mobile-nav-active #header .nav-menu a span,
.mobile-nav-active #header .nav-menu a:focus span {
	padding: 0 5px 0 7px;
	color: #45505b;
}

.mobile-nav-active #header .nav-menu a:hover span,
.mobile-nav-active #header .nav-menu .active span,
.mobile-nav-active #header .nav-menu .active:focus span,
.mobile-nav-active #header .nav-menu li:hover > a span {
	color: #fff;
}

.bxs-x-square:before {
	color: #0563bb;
}

@media (max-width: 767px) {
	#header {
		z-index: 2;
		width: 300px;
		background: #fff;
		border-right: 1px solid #e6e9ec;
		left: -300px;
	}

	section,
	#footer {
		padding: 30px 1rem;
	}

	.section-content h3 {
		text-align: center;
	}
}
